import React, { useState, useEffect } from 'react';
import { Container, Typography, Grid, Box, Paper } from '@mui/material';
import StockMarket from './components/StockMarket';
import AITrader from './components/AITrader';
import StockChart from './components/StockChart';
import NewsFeed from './components/NewsFeed';
import PortfolioDetails from './components/PortfolioDetails';

function App() {
  const initialStocks = { ' A': 10, ' B': 15, ' C': 20 }; //     
  const [userPortfolio, setUserPortfolio] = useState({
    balance: 1000,
    stocks: { ...initialStocks },
  });
  const [aiPortfolio, setAiPortfolio] = useState({
    balance: 1000,
    stocks: { ...initialStocks },
  });
  const [stocks, setStocks] = useState([
    { name: ' A', price: 100, history: [100] },
    { name: ' B', price: 150, history: [150] },
    { name: ' C', price: 200, history: [200] },
  ]);
  const [news, setNews] = useState([]);

  const calculatePortfolioValue = (portfolio) => {
    let stockValue = Object.keys(portfolio.stocks).reduce((total, stockName) => {
      const stock = stocks.find(s => s.name === stockName);
      return total + (portfolio.stocks[stockName] * stock.price);
    }, 0);
    return portfolio.balance + stockValue;
  };

  const updateStockPrices = () => {
    setStocks(stocks.map(stock => {
      const newPrice = Math.max(stock.price + (Math.random() * 20 - 10), 0.0001); //    
      return {
        ...stock,
        price: newPrice,
        history: [...stock.history, newPrice], //    
      };
    }));
  };

  const generateNews = () => {
    const newsEvents = [
      { name: ' A', impact: '', reason: '      .', effect: 1.15 },
      { name: ' B', impact: '', reason: '       .', effect: 0.85 },
      { name: ' C', impact: '', reason: '   ,     .', effect: 1.20 },
      { name: ' A', impact: '', reason: '     ,    .', effect: 0.80 },
      { name: ' B', impact: '', reason: '        .', effect: 1.10 },
      { name: ' C', impact: '', reason: '     ,     .', effect: 0.75 }
    ];

    if (Math.random() < 0.1) { // 10%  
      const event = newsEvents[Math.floor(Math.random() * newsEvents.length)];
      setStocks(stocks.map(stock => (
        stock.name === event.name 
          ? { ...stock, price: stock.price * event.effect, history: [...stock.history, stock.price * event.effect] }
          : stock
      )));
      setNews(prevNews => [`: ${event.name} ${event.impact}  ! : ${event.reason}`, ...prevNews]);
    }
  };

  useEffect(() => {
    const interval = setInterval(() => {
      updateStockPrices();
      generateNews();
    }, 1000); //   1 
    return () => clearInterval(interval);
  }, [stocks]);

  return (
    <Container>
      <Typography variant="h3" align="center" sx={{ marginY: 4 }}>   </Typography>

      <Grid container spacing={4}>
        {/*   */}
        <Grid item xs={8}>
          <Paper elevation={3} sx={{ padding: 2, borderRadius: 2 }}>
            <StockChart stocks={stocks} />
          </Paper>

          {/*       */}
          <Box sx={{ marginTop: 2 }}>
            <Paper elevation={3} sx={{ padding: 2, borderRadius: 2 }}>
              <Typography variant="h6">
                 :
              </Typography>
              <StockMarket 
                stocks={stocks} 
                portfolio={userPortfolio} 
                setPortfolio={setUserPortfolio} 
              />
            </Paper>
          </Box>
        </Grid>

        {/*       */}
        <Grid item xs={4}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Paper elevation={3} sx={{ padding: 2, borderRadius: 2 }}>
                <Typography variant="h6">
                   :    ${userPortfolio.balance.toFixed(2)}, 
                   : ${calculatePortfolioValue(userPortfolio).toFixed(2)}
                </Typography>
                <PortfolioDetails portfolio={userPortfolio} />
              </Paper>
            </Grid>

            <Grid item xs={12}>
              <Paper elevation={3} sx={{ padding: 2, borderRadius: 2 }}>
                <Typography variant="h6">
                   :    ${aiPortfolio.balance.toFixed(2)}, 
                   : ${calculatePortfolioValue(aiPortfolio).toFixed(2)}
                </Typography>
                <PortfolioDetails portfolio={aiPortfolio} />
              </Paper>
            </Grid>
          </Grid>
        </Grid>
      </Grid>

      {/*   AITrader */}
      <AITrader 
        stocks={stocks} 
        portfolio={aiPortfolio} 
        setPortfolio={setAiPortfolio} 
      />

      {/*   */}
      <Box sx={{ marginTop: 4 }}>
        <Paper elevation={3} sx={{ padding: 2, borderRadius: 2 }}>
          <NewsFeed news={news} />
        </Paper>
      </Box>
    </Container>
  );
}

export default App;
